const Buttons = ({ currButton, setCurrButton }) => {

    const handleCurrButton = (value) => {
        setCurrButton(value)
        console.log(currButton)
    }

    return (
        <div className="buttons">
            <h2 onClick={() => handleCurrButton("overview")} className={currButton === "overview" ? "buttons__btn btnIsActive" : "buttons__btn"}>OVERVIEW</h2>
            <h2 onClick={() => handleCurrButton("structure")} className={currButton === "structure" ? "btnIsActive buttons__btn" : "buttons__btn"}>STRUCTURE</h2>
            <h2 onClick={() => handleCurrButton("surface")} className={currButton === "surface" ? "btnIsActive buttons__btn" : "buttons__btn"}>SURFACE</h2>
        </div>
    );
}

export default Buttons