import Image from '../../ressources/image/Import';

const PlanetImg = ({ currPlanet }) => {


    return (
        <div className="planet">
            {currPlanet ? <img src={Image(currPlanet.toLowerCase())} alt={currPlanet} /> : <img src={Image('earth')} alt="Earth" />}
        </div>
    );
}

export default PlanetImg;