import Data from '../../ressources/Data'
import { ExternalLink } from 'react-external-link'
import MediaQuery from 'react-responsive';
import Buttons from './Buttons';

const PlanetDesc = ({ currPlanet, currButton, setCurrButton }) => {

    // console.log('currPlanet', currPlanet)
    // console.log('data', Data)

    return (
        <div>
            {currPlanet ? Data.map((el, index) => (
                currPlanet === el.name ?
                    <section key={index}>
                        <article className="copy">
                            <div className="copy__leftContent">
                                <h2 className="copy__title">{el.name}</h2>
                                {currButton === "overview" ?
                                    <p className="copy__content">{el.desc}</p>
                                    : currButton === "structure" ?
                                        <p className="copy__content">{el.structure}</p>
                                        : currButton === "surface" ?
                                            <p className="copy__content">{el.surface}</p>
                                            : ""
                                }
                                <p className="copy__link">Source : <ExternalLink href={el.wiki} className="copy__link__wiki">Wikipedia<i className="fas fa-sign-out-alt copy__link__icon"></i></ExternalLink></p>
                            </div>
                            <MediaQuery minWidth={601}>
                                <Buttons currButton={currButton} setCurrButton={setCurrButton} className="copy__rightContent" />
                            </MediaQuery>
                        </article>
                        <article className="infos__container">
                            <div className="infos">
                                <p className="infos__title">ROTATION TIME</p>
                                <p className="infos__num">{el.rotation}</p>
                            </div>
                            <div className="infos">
                                <p className="infos__title">REVOLUTION TIME</p>
                                <p className="infos__num">{el.revolution}</p>
                            </div>
                            <div className="infos">
                                <p className="infos__title">RADIUS</p>
                                <p className="infos__num">{el.radius}</p>
                            </div>
                            <div className="infos">
                                <p className="infos__title">AVERAGE TEMP.</p>
                                <p className="infos__num">{el.avgTemp}</p>
                            </div>
                        </article>
                    </section>
                    : ""
            )) :
                <section>
                    <article className="copy">
                        <div className="copy__leftContent">
                            <h2 className="copy__title">Earth</h2>
                            {currButton === "overview" ?
                                <p className="copy__content">Earth is the third planet from the Sun and the only astronomical object known to harbor and support life. About 29.2% of Earth's surface is land consisting of continents and islands. The remaining 70.8% is covered with water, mostly by oceans, seas, gulfs, and other salt-water bodies, but also by lakes, rivers, and other freshwater, which together constitute the hydrosphere.</p>
                                : currButton === "structure" ?
                                    <p className="copy__content">Lorem ipsum dolor sit amet consectetur, adipisicing elit. Vitae natus quo soluta perferendis, omnis, ducimus magni sunt qui quam veniam accusantium consequuntur eaque, voluptate dicta ullam tempora debitis distinctio consectetur.</p>
                                    : currButton === "surface" ?
                                        <p className="copy__content">Lorem, ipsum dolor sit amet consectetur adipisicing elit. Incidunt aperiam sequi ullam velit id voluptates optio maxime fugit ipsam dolore rerum, sint dignissimos tempora distinctio commodi omnis a recusandae consequuntur.</p>
                                        : ""
                            }
                            <p className="copy__link">Source : <ExternalLink href="https://fr.wikipedia.org/wiki/Terre" className="copy__link__wiki">Wikipedia<i className="fas fa-sign-out-alt copy__link__icon"></i></ExternalLink></p>
                        </div>
                        <MediaQuery minWidth={601}>
                            <Buttons currButton={currButton} setCurrButton={setCurrButton} className="copy__rightContent" />
                        </MediaQuery>
                    </article>
                    <article className="infos__container">
                        <div className="infos">
                            <p className="infos__title">ROTATION TIME</p>
                            <p className="infos__num">0.99 DAYS</p>
                        </div>
                        <div className="infos">
                            <p className="infos__title">REVOLUTION TIME</p>
                            <p className="infos__num">365 DAYS</p>
                        </div>
                        <div className="infos">
                            <p className="infos__title">RADIUS</p>
                            <p className="infos__num">6.371 KM</p>
                        </div>
                        <div className="infos">
                            <p className="infos__title">AVERAGE TEMP.</p>
                            <p className="infos__num">16°C</p>
                        </div>
                    </article>
                </section>
            }
        </div>
    );
}

export default PlanetDesc;