import Buttons from "./Buttons"
import PlanetImg from "./PlanetImg"
import './Main.css';
import PlanetDesc from "./PlanetDesc";
import { useState } from "react";
import MediaQuery from "react-responsive";

const Main = ({ currPlanet }) => {

    const [currButton, setCurrButton] = useState("overview");

    return (
        <section className="main">
            <MediaQuery maxWidth={600}>
                <Buttons currButton={currButton} setCurrButton={setCurrButton} />
            </MediaQuery>
            <PlanetImg currPlanet={currPlanet} />
            <PlanetDesc currPlanet={currPlanet} currButton={currButton} setCurrButton={setCurrButton} />
        </section>
    );
}

export default Main;