import Nav from "./Nav";
import Burger from "./Burger";
import './Header.css';
import MediaQuery from "react-responsive";

const Header = ({ setCurrPlanet, currPlanet }) => {
    return (
        <section className="header">
            <h1 className="header__title">THE PLANETS</h1>
            <MediaQuery maxWidth={600}>
                <Burger setCurrPlanet={setCurrPlanet} currPlanet={currPlanet} className="Burger" />
            </MediaQuery>
            <MediaQuery minWidth={601}>
                <Nav setCurrPlanet={setCurrPlanet} currPlanet={currPlanet} className="Nav" />
            </MediaQuery>
        </section>
    );
}

export default Header;