
const NavModal = ({ handleModal, setCurrPlanet, currPlanet }) => {

    const handleCurrPlanet = (value) => {
        setCurrPlanet(value)
        console.log(currPlanet)
        handleModal()
    }

    return (
        <div>
            <div className="bgBlurr"></div>
            <div className="navModal">
                <i onClick={handleModal} className="fas fa-times navModal__close"></i>
                <ul>
                    <li onClick={() => handleCurrPlanet("Mercury")} className={currPlanet === "Mercury" ? "isActive" : ""}>MERCURY</li>
                    <li onClick={() => handleCurrPlanet("Venus")} className={currPlanet === "Venus" ? "isActive" : ""}>VENUS</li>
                    <li onClick={() => handleCurrPlanet("Earth")} className={currPlanet === "Earth" ? "isActive" : ""}>EARTH</li>
                    <li onClick={() => handleCurrPlanet("Mars")} className={currPlanet === "Mars" ? "isActive" : ""}>MARS</li>
                    <li onClick={() => handleCurrPlanet("Jupiter")} className={currPlanet === "Jupiter" ? "isActive" : ""}>JUPITER</li>
                    <li onClick={() => handleCurrPlanet("Saturn")} className={currPlanet === "Saturn" ? "isActive" : ""}>SATURN</li>
                    <li onClick={() => handleCurrPlanet("Uranus")} className={currPlanet === "Uranus" ? "isActive" : ""}>URANUS</li>
                    <li onClick={() => handleCurrPlanet("Neptune")} className={currPlanet === "Neptune" ? "isActive" : ""}>NEPTUNE</li>
                </ul>
            </div>
        </div>
    );
}

export default NavModal;