const Nav = ({ handleModal, setToggleModal, setCurrPlanet, currPlanet }) => {

    const handleCurrPlanet = (value) => {
        setCurrPlanet(value)
        console.log(currPlanet)
    }

    return (
        <ul className="nav__links">
            <li onClick={() => handleCurrPlanet("Mercury")} className={currPlanet === "Mercury" ? "isActive nav__links__link" : "nav__links__link"}>MERCURY</li>
            <li onClick={() => handleCurrPlanet("Venus")} className={currPlanet === "Venus" ? "isActive nav__links__link" : "nav__links__link"}>VENUS</li>
            <li onClick={() => handleCurrPlanet("Earth")} className={currPlanet === "Earth" ? "isActive nav__links__link" : "nav__links__link"}>EARTH</li>
            <li onClick={() => handleCurrPlanet("Mars")} className={currPlanet === "Mars" ? "isActive nav__links__link" : "nav__links__link"}>MARS</li>
            <li onClick={() => handleCurrPlanet("Jupiter")} className={currPlanet === "Jupiter" ? "isActive nav__links__link" : "nav__links__link"}>JUPITER</li>
            <li onClick={() => handleCurrPlanet("Saturn")} className={currPlanet === "Saturn" ? "isActive nav__links__link" : "nav__links__link"}>SATURN</li>
            <li onClick={() => handleCurrPlanet("Uranus")} className={currPlanet === "Uranus" ? "isActive nav__links__link" : "nav__links__link"}>URANUS</li>
            <li onClick={() => handleCurrPlanet("Neptune")} className={currPlanet === "Neptune" ? "isActive nav__links__link" : "nav__links__link"}>NEPTUNE</li>
        </ul>
    );
}

export default Nav;