import { useState } from "react";
import NavModal from "./NavModal"

const Burger = ({ setCurrPlanet, currPlanet }) => {

    const [toggleModal, setToggleModal] = useState(false);

    const handleModal = () => {
        setToggleModal(!toggleModal)
        // console.log(toggleModal)
    }

    return (
        <div>
            <i onClick={handleModal} className="fas fa-bars header__burger"></i>
            {toggleModal ? <NavModal handleModal={handleModal} setToggleModal={setToggleModal} setCurrPlanet={setCurrPlanet} currPlanet={currPlanet} /> : ""}
        </div>
    );
}

export default Burger;